
.product img {
    max-height: 45%;
    max-width: 100%;
  }
  
.product{
    height: 50vh;
    width: 30vw;
    padding: 3% 0% 0% 2%;
    transition: ease-in-out 0.1s;
    /* border-radius: 10px; */
  }
  .product:hover{
    box-shadow: 5px 5px 5px 5px #bbbbbb77;
    z-index: 1;
  
  }
  
  .rating{
    background-color: rgb(0, 134, 11);
    color: aliceblue;
    border-radius: 5px;
    padding: 0px;
    width: 40px;
    font-size:small;
  }
  
  .meta{
    display: flex;
  }
  .stock{
    color: grey;
    font-size: small;
    font-weight: 450;
  }
  
  .ProductList{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* align-items: center; */
  }

  .cross{
    color: grey;
    text-decoration: line-through;
  }

  .price{
    font-weight: bold;
  }

  .product button{
    background-color: rgba(128, 128, 128, 0.392);
    color: white; 
    border: none;
    border-radius: 7px;
    padding: 5px;
    transition: ease-in-out 0.15s;
  
  }

  .product button:hover{
    background-color: rgba(80, 80, 80, 0.695);
  }


  