.Nav{
    display: flex;
    justify-content:space-between;
    align-items: baseline;
    background-color: black;
    color: aliceblue;
    padding: 1% 5% 1% 5%;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
}

.Nav input{
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 10%;
    position: relative;
    bottom: 3px;

    
}

.searchbox {
    width: 40%;
    height: 10%;

}

.Nav :link{
    text-decoration: none;
    color: aqua;   
}

.Nav img{
    height:20px;
    filter: invert(1);
    position: relative;
    top: -5px;
    padding: 0px 0px 0px 10px;
}

.Nav label{
    font-size: 130%;
    font-weight: 700;
    color: white;
}

.Nav label:hover{
    color: grey;
}








